.main-container{
	position: relative;
	width: 490px;
	
	
	
}
/*creating line for timeline*/
.main-container::after{
	content: '';
	position: absolute;
	width: 3px;
	background-color: blueviolet;
	top:0;
	bottom: 0;
	left: 50%;
	margin-left: -1px;
}
/*Adjusting box of all content*/
.text-wrapper{
	padding: 2px 20px;
	position: relative;
	width:50.5%;
	box-sizing: border-box;
	margin: 30px 0;
}
.text-wrapper::after{
	content: '';
	position: absolute;
	width: 18px;
	height: 19px;
	right: -8px;
	background-color: blueviolet;
	top:6px;
	border-radius: 50%;
	z-index: 1;
}
/*for left events*/
.left{
	left: 0;
}
/*for right events*/
.right{
	left:50%;
}
.right::after{
	left:-10px;
}
/*content box colour padding and radius for circular corner*/
.content{
	padding: 10px 10px 10px 15px;
}
/*setting text property of event heading*/
.content h3{
	text-transform: uppercase;
	font-size: 14px;
	color: #212121;
	letter-spacing:1px;
}
/*setting text property of event content*/
.content p{
	color: #616161;
	font-weight: 250;
	font-size: 14px;
	margin-top: 2px;
}
 


